#Container {
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
  padding: 10px;
  border-radius: 10px;
  filter: drop-shadow(20px 20px 30px rgba(10, 197, 160, 0.647));
}

#mainContainer {
  padding: 10px;
  width: auto;
  max-width: 56%;
  flex-wrap: wrap;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: rgba(10, 0, 14, 0.263);
  color: rgb(142, 26, 150);

}

#mainContainer  h2 {
  margin-top: 12px ;
  color: rgba(240, 248, 255, 0.316);
}
